import React, {  useEffect, useState } from 'react';
import { Container } from 'react-bootstrap';
import HTMLFlipBook from 'react-pageflip';
import './MenuFlipbook.css';

const menuImages = [
  "./images/menu.gif",
  "./images/pg1.gif",
  "./images/pg2.png",
  "./images/pg3.png", 
  "./images/lastpg.png", // Add more image paths as needed
];

function MenuFlipbook() {
  const [dimensions, setDimensions] = useState({ width: 500, height: 600 });

  const handleResize = () => {
    const containerWidth = document.getElementById('menu').clientWidth;
    const width = Math.min(containerWidth, 500);
    const height = width * 1.2; // Maintain aspect ratio (5:6)
    setDimensions({ width, height });
  };

  useEffect(() => {
    handleResize(); // Set initial size
    window.addEventListener('resize', handleResize); // Adjust on window resize

    return () => window.removeEventListener('resize', handleResize);
  }, []);

  return (
    <Container fluid id="menu" className="my5">
      
      <h2>Our Menu</h2>
      <h5>Turn the page to discover more tea</h5>
      <div className="flipbook-wrapper">
        <HTMLFlipBook width={dimensions.width} height={dimensions.height} className="flip-book">
          {menuImages.map((image, index) => (
            <div className="page" key={index}>
              <img src={image} alt={`Menu page ${index + 1}`} className="menu-image" />
            </div>
          ))}
        </HTMLFlipBook>
      </div>
    
    </Container>
  );
}

export default MenuFlipbook;
