import React from 'react';
import { Container, Row, Col, Button } from 'react-bootstrap';

function Home() {
  return (
    <Container fluid className="home-page">
      <Row className="justify-content-left home-content">
        <Col md={8}>
          <h1>Discover the magic of Tea Daze </h1>
          <p>Step into Tea Daze, where each brew is a revelation. Discover our curated selection of extraordinary teas and elevate your moments.</p>
          <Button href="#menu"className="custom-button"  size="lg">
            Explore Our Menu
          </Button>
        </Col>
      </Row>
      
    </Container>
  );
}

export default Home;
