import React from 'react';
import {Button} from 'react-bootstrap';




const Footer = () => {
  return (
    <footer className="footer bg-light text-center text-lg-start" id='contact'>
    <div className="container p-4">
      <h2 className="footer-title mb-4">Visit Tea Daze</h2>
      <div className="row">
        <div className="col-lg-6 col-md-12">
          {/* <img src="/logo2.png" alt="Tea Daze Logo" className="footer-logo mb-2" /> */}
          <p className="footer-about">
            Tea Daze offers a wide variety of teas to enchant your senses and elevate your moments. Discover the perfect brew for every mood.
          </p>
          <Button href="#about"className="custom-button1"  size="md">
            About Us
          </Button>
        </div>
        {/* <div className="col-lg-4 col-md-12 mb-4 mb-md-0">
            <img src="./ParkingAvailable.png"alt="Footer-image" className="footer-image img-fluid" />
          </div> */}
        <div className="col-lg-6 col-md-12 details">
          <p className="footer-location">
          <i className="fas fa-map-marker-alt"></i>24/R , North Street, Nagercoil.
          </p>
          <p className="footer-phone">
          <i className="fas fa-phone-alt"></i>9876543219
          </p>
        </div>
        
      </div>
    </div>
    <div className="footer-copyright text-center py-3">
      &copy; {new Date().getFullYear()} Tea Daze🍵. All rights reserved.
    </div>
    </footer>
  );
};

export default Footer;
